<template>
	<div>
  		<div id="myChart" :style="{width: '400px', height: '300px'}"></div>
  	</div>	
</template>


<script>
	export default {
	  name: 'hello',
	  data () {
	    return {
	      msg: 'Welcome to Your Vue.js App'
	    }
	  },
	  mounted(){
	    this.drawLine();
	  },
	  methods: {
	    drawLine(){
	        // domecharts
	        let myChart = this.$echarts.init(document.getElementById('myChart'))
	        // 
	        myChart.setOption({
	        	title : {
				        text: '',
				        subtext: '',
				        x:'center'
				    },
	            tooltip: {},
	            xAxis: {
	                data: ["7","8","9","10","11","12"]
	            },
	            yAxis: {},
	            series: [{
	                name: '',
	                type: 'bar',
	                data: [3000, 3020, 3600, 2880, 4550, 4880]
	            }]
	        });
	    }
	  }
	}	

</script>

<style scoped>
	#myChart{
		margin-left: 280px;
	}
</style>


