<template>
  <div>
    <div class="top-btn">
      <el-input v-model="input" placeholder="" class="one"></el-input>
      <el-button type="primary" icon="search" @click="nameSearch(input)"></el-button>
      <el-button type="primary" icon="edit" @click='addInf()'></el-button>
      <el-button type="primary" icon="delete" @click='moreDel(mor)'></el-button>
      <el-button type="primary" icon="search" @click='downData()'></el-button>
    </div>
    <div class="table">
      <el-table ref="multipleTable" :data="ajaxRes" border tooltip-effect="dark" style="width: 99.3%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center">
        </el-table-column>
        <el-table-column label="" width="90" align="center" >
            <template scope="scope">
                <template class="">
                    <p>{{scope.$index+1}}</p>
                </template> 
            </template>
        </el-table-column>
        <el-table-column label="" width="120" align="center">
          <template scope="scope">{{ scope.row.studentName }}</template>
        </el-table-column>
        <el-table-column label="" width="90" align="center">
            <template scope="scope">
                <p v-if='scope.row.sex=="man"'></p>
                <p v-else></p>
            </template>
        </el-table-column>
         <el-table-column label="" width="90" align="center">
            <template scope="scope">
                <p v-if='scope.row.minzu==10010'></p>
                <p v-else-if='scope.row.minzu==10011'></p>
                <p v-else-if='scope.row.minzu==10013'></p>
                <p v-else></p>
            </template>
        </el-table-column>
        <el-table-column label="" width="120" align="center">
            <template scope="scope">
                <p v-if='scope.row.xueyuan==="40011"'></p>
                <p v-else-if='scope.row.xueyuan==="40010"'></p>
                <p v-else-if='scope.row.xueyuan==="40013"'></p>
                <p v-else></p>
            </template>
        </el-table-column>       
        <el-table-column label="" width="120" align="center">
            <template scope="scope">
                <p>{{scope.row.ruxiaodate | capitalize}}</p>
            </template>
        </el-table-column> 
        <el-table-column label="" width="120" align="center">
            <template scope="scope">{{ scope.row.address }}</template>
        </el-table-column>
        <el-table-column label="" width="120" align="center">
            <template scope="scope">
                <p v-if='scope.row.quanrizhi==true'></p>
                <p v-else></p>
            </template>
        </el-table-column>          
        <el-table-column prop="word6" label="" width="175"  align="center">
          <template scope="scope">
            <el-button type="info" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="foot">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage1"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
    import {DateDiff} from '../js/dateDiff.js';
    export default {
        data() {
            return {
                input: '',
                value: '',  
                tableData3:[], 
                currentPage1: 1,
                ajaxRes:null, 
                pageSize:10,
                currPage:1,
                total:0      
            }
        },
        mounted(){
            this.Ajax()
        },
        //
        filters:{
            capitalize:function(v){
                if(v==''){
                    return ''
                }else{
                   return DateDiff(v) 
                }
                
            }
        },
        methods:{
            //
            Ajax(){
                let vm=this;
                let par={
                    "pageSize":10,
                    "currPage":this.currPage,
                    "studentName":this.input,
                };
                this.$http.get('http://localhost:3000/api/queryStudents',{params:par})
                .then(res=>{
                    console.log('ajax',res)
                    vm.ajaxRes=res.body.data.data;
                    vm.total=res.body.data.total
                },res=>{

                    console.log('ajax',res)
                })                 
            },
            //
            handleCurrentChange(v){
                this.currPage=v;
                this.Ajax()
            },
            //
            handleSizeChange(){

            },
            //
            handleSelectionChange(v,index){
                this.mor=v
            },
            //
            nameSearch(v){
                this.input=v;
                this.Ajax();
            },
            //
            handleEdit(index, row) {
                this.$router.push( { path: '/studentsInf/studentsUpdata', query: { id: row._id } } );
            },
            //
            handleDelete(index, row) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                this.delData(row);
                this.Ajax();                    

                });
            },
            //
            delData(row){
                this.$http.get('http://localhost:3000/api/delStudents',{ params:{ id:row._id } })
                this.$message({
                    message: '!'

                }); 
                        

                this.Ajax()
               
            },
            moreDel(v){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    for(let index in v){
                        this.delData(v[index]);
                    }       
                });               
                this.Ajax();
            },
            //
            addInf(){
                this.$router.push('/studentsInf/studentsAdd')
            },
/*            downData(){
                let vm = this;

                require.ensure([], () => {
                    const { export_json_to_excel } = require('vendor/Export2Excel');
                    const tHeader = ['1', '2', '3', '4', '5'];
                    const filterVal = ['chnlId', 'hisChnlId', 'chnlName', 'state', 'isavailable'];
                    const list = vm.list;
                    const data = vm.formatJson(filterVal, list);
                    export_json_to_excel(tHeader, data, 'excel');
                })
            },
            formatJson(filterVal, jsonData) {
                return jsonData.map(v => filterVal.map(j => v[j]))
            }        
            */

        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .top-btn{
    margin-left: 240px;
    margin-top: 20px;
  }
  .one{
    width: 160px;
    margin-right: 8px;
  }
  .table{
    margin-left: 240px;
    margin-top: 30px;
  }

  .foot{
    margin-left: 250px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
</style>


